.body-buttonNormal {
    display: flex;
    padding: 5px 10px;
    border-radius: 10px;
    background: var(--greenColorHot);
    color: var(--greenColorBegin_Background);
    cursor: pointer;
    align-items: center;
    font-size: 20px;
}
.body-buttonNormal.isNo{
    color: var(--greenColorHot);
    background: var(--greenColorBegin_Background);
}
.container-buttonNormal {
    width: max-content;
    height: max-content;
    margin-right: 5px;
    margin-bottom: 5px;
}
