.container-popupMessenger
{
    position: relative;
    
}
.body-popupMessenger
{
    display: flex;
    flex-direction: column;
    background: var(--greenColorEnd_Background);
    border: 2px solid var(--greenColorHot);
    border-radius: 8px;
    max-width: 400px;
}
.content-popupMessenger
{
    max-height: 350px;
    padding: 5px 5px;
    overflow-y: auto;
    scroll-behavior: smooth;
    min-height: 350px;
}
.footer-popupMessenger
{
    padding: 5px 10px;
    border-top: 2px solid var(--greenColorHot);
    position: relative;
}
.content-formMessenger
{
    display: flex;
    align-items: center;
}
.header-popupMessenger
{
    padding: 5px 10px;
    border-bottom: 4px solid var(--greenColorHot);
}
.container-showMoveDownMess
{
    position: absolute;
    top: -70px;
    left: 50%;
    transform: translateX(-50%);
}

