.contentLeft-formMessenger
{
    display: flex;
    align-items: center;
}
.contentCenter-formMessenger
{
    width: 1px;
    flex-grow: 1;
}
.section-iptText_formMessenger
{
    display: flex;
    border: 1px solid var(--greenColorHot);
    border-radius: 6px;
    justify-content: space-between;
    align-items: center;
}
.iptText-formMessenger
{
    background: transparent;
    outline: none;
    border: none;
    color: var(--blackColorTextDefault);
    padding: 5px 10px;
    resize: none;
    flex: 1;
    font-size: 20px;
}.iptText-formMessenger::placeholder
{
    color: var(--blackColorTextDefault);
}
.iptFile-formMessenger
{
    padding: 20px 5px;
    display: flex;
    overflow-x: scroll;
    
}
.mess_be_typing
{
    position: absolute;
    top: -60px;
    left: 6px;
    display: flex;
    align-items: center;
    width: max-content;
    height: max-content;

}
.mess_be_typing img
{
    object-fit: cover;
    /* transform: scale(0.2); */
    width: 60px;
    height: 60px;
}
