.content-message
{
    display: flex;
    align-items: center;
    /* width: 90%; */
}
.content-message.isMe
{
    flex-direction: row-reverse;
}
.contentCenter-message
{
    margin:0;
    margin-right: 80px;
}
.isMe .contentCenter-message
{
    margin:0;
    margin-left: 80px !important ;

}
.sessionContent-message img,.sessionContent-message video
{
    width: 100%;
    
}
.sessionContent-message 
{
    display: flex;
    align-items: center;
    height: max-content;
    padding: 5px 10px;
    border-radius: 10px;
    background: var(--greenColorHot);
    max-width: max-content;
    position: relative;
}
.sessionContent-message + .sessionContent-message
{
    margin-top: 10px;
}
.container-message + .container-message
{
    margin-top: 10px;
}
.opt_sessionContent-message
{
    position: absolute;
    right: 0;
    transform: translateX(+100%);
    display: none;
    z-index: 4;
    /* padding: 10px 5px; */
    height: 100%;
    align-items: center;
}
.isMe .opt_sessionContent-message
{
    right: unset;
    left: 0 !important;
    transform: translateX(-100%) !important;
}
.content-message:hover .opt_sessionContent-message
{
display: flex;
}
.contentCenter-message 
{
    display: flex;
    flex-direction: column;
    
}